<template>
  <div id="app">
    <Navbar />
    <div class="container">
      <router-view/>
    </div>
    <div class="small">
      <quick-menu  position="bottom-left" backgroundColor ="#70AB8F" :menu-count='4' :icon-class='["fa fa-address-book", "fa fa-home", "fa fa-line-chart", "fa fa-upload"]' :menu-url-list='["#/cont","#","#","#/"]'></quick-menu>
    </div>
  </div>
  <!-- background-color: #FFFCEC-->
</template>

<script>
import Navbar from "./components/Navbar";
import quickMenu from "vue-quick-menu";
export default {
  name: "App",
  components: {
    Navbar,
    quickMenu
  }
};
</script>

<style>
nav {
  margin-bottom: 10px;
}
h3, h4 {
  color: #DC5B21
}
body {
  background-image: url('./assets/wood-wallpaper-45.jpg')
  
}
#preencher {
        background-color: white;
        opacity: .8;
        padding: 3%
    }
</style>



